import React from 'react';
import './FeedTab.css'


const FeedTab = () => (
    <div className="feed-tab">
        <span className="feed-tab-tab for-you">For you</span>
        <span className="feed-tab-tab">Near you</span>
    </div>
)

export default FeedTab;


